<template>
  <div class="demo-collapse" v-show="titlem">
    <el-collapse v-model="num" accordion>
      <el-collapse-item :title="titlem" name="1">
        <div class="list-item" v-for="(i,y) in listArr" :key="y" @click="actClick(y+1)"
             :class="(activeName==y+1)?'isAct':''">
          <span class="sidebar-link">{{ i.title }}</span>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script setup>
import {onMounted, reactive, ref, watch} from "vue";
import {mainStore} from "@/pinia";

const store = mainStore()
const props = defineProps(['leftList', 'titles'])

const listArr = ref([])
const titlem = ref('')
//折叠栏大类
const num = ref(store.collapseNum)
//小类折叠栏
const activeName = ref(store.collapseAct)
// v-bind绑定左侧折叠栏css属性
const topback = ref(store.background)
const topColor = ref(store.background === '#181818' ? '#fff' : '#181818')

const actClick = e => {
  // activeName.value = e
  store.collapseAct = e
}

//监听主题颜色，更改折叠栏颜色
watch(() => store.background, (newValue, oldValue) => {
  topback.value = newValue
  topColor.value = newValue === '#181818' ? '#fff' : '#181818'
}, {deep: true})
//监听父组件传入的titles和leftList
watch(() => props.titles, (newValue) => {
  titlem.value = newValue
})
watch(() => props.leftList, (newValue) => {
  listArr.value = newValue
})
//监听小类折叠栏变化
watch(() => store.collapseAct, (newValue, oldValue) => {
  activeName.value = newValue
})
onMounted(() => {
  if (props.leftList.length > 0) {
    listArr.value = props.leftList
  }
  if (props.titles) {
    titlem.value = props.titles
  }
})
</script>


<style scoped lang="less">
.demo-collapse {
  padding: 56.25px 0;

  :deep(.el-collapse), :deep(.el-collapse-item__wrap), :deep(.el-collapse-item__header) {
    border: none
  }


  :deep(.el-collapse-item__header) {
    padding: 13.125px 56.25px 13.125px 46.875px;
    font-size: 18PX;
    color: #3EAF7C;
  }

  .list-item {
    cursor: pointer;

    .sidebar-link {
      font-size: 14PX;
      font-weight: 400;
      display: block !important;
      padding: 0.55rem 1rem 0.35rem 2.25rem;
      line-height: 1.7;
      box-sizing: border-box;

      &:hover {
        color: #3eaf7c;
      }
    }
  }

  .isAct {
    background: #D8EFE5;
    font-weight: 600;
    border-right: 8px solid #3eaf7c;
    color: #3eaf7c;
  }
}

:deep(.el-collapse-item__header) {
  background: v-bind(topback);
}

:deep(.el-collapse-item__content) {
  padding: 0;
  background: v-bind(topback);
  color: v-bind(topColor)
}
</style>