import {createRouter, createWebHashHistory, createWebHistory} from 'vue-router'

/*
*
* 1. import { useRouter,useRoute } from 'vue-router'
* 2. const router =  useRouter() // vue页面路由跳转
* 3. const route = useRoute()  // vue页面获取路由参数
* */


const routes = [
    {
        path: '/',
        name: 'login',
        component: () => import('@/views/login/login.vue'),
        meta: {
            title: 'login'
        }
    },
    {
        path: '/',
        name: "home",
        redirect: '/ybz',
        component: () => import('@/views/home/index.vue'),
        meta: {
            title: 'home',
            index: 1
        },
        children: [
            {
                path: 'ybz',
                name: 'ybz',
                component: () => import('@/views/home/pages/index.vue'),
                meta: {
                    title: 'yabaiz', childIndex: 1
                },
            },
            {
                path: 'doctor',
                name: 'doctor',
                component: () => import('@/views/home/pages/doctor/doctorIndex.vue'),
                meta: {title: 'Doctor', childIndex: 1}
            },
            {
                path: 'work',
                name: 'work',
                component: () => import('@/views/home/pages/work/work.vue'),
                meta: {title: 'Work', childIndex: 1}
            },
            {
                path: 'vue3Start',
                name: 'vue3Start',
                component: () => import('@/views/home/pages/vueStart/vue3/vue3Start.vue'),
                meta: {
                    title: 'vue3Start',
                    childIndex: 1,
                    leaveCaches: 'vue3Start', //离开时保活页面
                }
            }
        ]
    },
]


const router = createRouter({
    // history: createWebHistory(),
    history: createWebHashHistory(),
    routes
})

export default router
